<template>
  <div class="waterpool">
    <!-- 標題 -->
    <div class="row text-center" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >水庫</span
        >
      </div>
    </div>

    <el-row type="flex" align="middle" justify="center">
      <el-col :span="12">
        <!-- 圖標說明 -->
        <el-card class="iconExplainCard">
          <el-row type="flex" align="middle">
            <el-col :span="8" class="iconExplainCard_title">
              圖標
              <br />說明
            </el-col>
            <el-col :span="16">
              <div
                v-for="(item, index) in iconimg"
                :key="item"
                class="iconExplain"
              >
                <img :src="item" class="iconExplain_img" />
                <span>{{ iconExplain[index] }}</span>
              </div>
            </el-col>
          </el-row>
        </el-card>
        <!-- 地圖 -->
        <l-map
          style="height: 400px; width: 80%; margin: 0 auto"
          :zoom="zoom"
          :options="options"
          :center="center"
          v-if="text.length !== 0"
        >
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="(item, index) in MapData"
            :key="item.name"
          >
            <l-icon :icon-url="iconColor(index)" />
            <!-- 地標圖示 -->
            <l-tooltip :options="tooltipOptions">
              <div class="tooltip-content">
                <div>{{ item.name }}</div>
              </div>
            </l-tooltip>
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup style="text-align: center">
              <div>{{ item.name }}</div>
              <div>目前有效容量比 : {{ text[index].percent }}%</div>
            </l-popup>
          </l-marker>
        </l-map>
      </el-col>
      <!-- 阿公店水庫 -->
      <el-col :span="12" v-if="getDataFinished">
        <div id="w1" style="margin-bottom: 5%">{{ text[0].name }}</div>
        <el-row type="flex" style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic"
              @mouseover="animation('in', 'originalpic')"
              @mouseout="animation('out', 'originalpic')"
            >
              {{ text[0].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位 (m) : {{ text[0].capacitym }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[0].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas"
              @mouseover="animation('in', 'sineCanvas')"
              @mouseout="animation('out', 'sineCanvas')"
            ></canvas>
            <div id="percent1">有效容量比 : {{ text[0].percent }} %</div>
            <div>水位 (m) : {{ text[0].nowm }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[0].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間 : {{ text[0].time }}</div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <el-row>
      <!-- 澄清湖水庫 -->
      <el-col :span="12" v-if="getDataFinished">
        <div id="w2" style="margin-bottom: 5%">{{ text[1].name }}</div>
        <el-row type="flex" style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic2"
              @mouseover="animation('in', 'originalpic2')"
              @mouseout="animation('out', 'originalpic2')"
            >
              {{ text[1].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位 (m) : {{ text[1].capacitym }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[1].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas2"
              @mouseover="animation('in', 'sineCanvas2')"
              @mouseout="animation('out', 'sineCanvas2')"
            ></canvas>
            <div id="percent2">有效容量比 : {{ text[1].percent }} %</div>
            <div>水位 (m) : {{ text[1].nowm }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[1].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間 : {{ text[1].time }}</div>
          </el-col>
        </el-row>
      </el-col>
      <!-- 鳳山水庫 -->
      <el-col :span="12" v-if="getDataFinished">
        <div id="w3" style="margin-bottom: 5%">{{ text[2].name }}</div>
        <el-row type="flex" style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic3"
              @mouseover="animation('in', 'originalpic3')"
              @mouseout="animation('out', 'originalpic3')"
            >
              {{ text[2].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位 (m) : {{ text[2].capacitym }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[2].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas3"
              @mouseover="animation('in', 'sineCanvas3')"
              @mouseout="animation('out', 'sineCanvas3')"
            ></canvas>
            <div id="percent3">有效容量比 : {{ text[2].percent }} %</div>
            <div>水位 (m) : {{ text[2].nowm }}m</div>
            <div>
              水庫容量 (萬立方公尺) : {{ text[2].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間 : {{ text[2].time }}</div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <!-- </div> -->

    <!-- 水庫水質查詢標題 -->
    <div class="row" style="margin: 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animated infinite fadeInUp text-center title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >水質查詢</span
        >
      </div>
    </div>
    <el-collapse v-model="activeNames">
      <el-collapse-item title="卡爾森指數" name="1">
        <el-row :gutter="20">
          <el-col :span="12">
            <div>
              用於評估水庫水質優養程度的指標為「<b>卡爾森指數</b>，Carlson
              trophic state index」，簡稱<b>CTSI</b>
            </div>
            <div>
              CTSI係以<b>水中的透明度（SD）</b>、<b>葉綠素a（Chl-a）</b>及<b>總磷（TP）</b>等三項水質參數之濃度值進行計算
            </div>
            <div>再以其計算所得之指標值，判定水庫水質之優養程度</div>
            <el-table :data="CTSI_tableData" style="width: 100%">
              <el-table-column prop="CTSI" label="CTSI指標值" width="180">
              </el-table-column>
              <el-table-column prop="degree" label="水庫優養程度" width="180">
              </el-table-column>
            </el-table>
            <div style="font-weight: bold">
              點擊右方連結以了解詳細卡爾森指數算法
              <a href="https://wq.epa.gov.tw/Code/Business/Standard.aspx"
                >卡爾森指數詳細說明</a
              >
            </div>
          </el-col>
          <el-col :span="12">
            <chart
              :options="ChartOption"
              v-if="ChartOption.series.length === 2"
              height="300"
              style="width: 100%; height: 300px"
            ></chart>
          </el-col>
        </el-row>
      </el-collapse-item>
      <el-collapse-item title="各測站進階歷史紀錄搜尋" name="2">
        <el-row>
          <el-col :span="6">
            <el-select v-model="searchData.dam" placeholder="水庫">
              <el-option
                v-for="item in searchDamName"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-select v-model="searchData.year" placeholder="年分">
              <el-option
                v-for="item in searchDamYear"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-button @click="search">查詢</el-button>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <l-map
              style="height: 300px; width: 80%; margin: 0 auto"
              :zoom="zoom"
              :options="options"
              :center="center"
              v-if="text.length !== 0"
            >
              <!-- 載入地圖資料 -->
              <l-tile-layer :url="url" :attribution="attribution" />
              <!-- 地標 -->
              <l-marker
                ref="location"
                :lat-lng="item.local"
                v-for="item in DamMapData"
                :key="item.name"
              >
                <!-- <l-icon
              :icon-url="
                require('https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png')
              "
            /> -->
                <!-- 地標圖示 -->
                <l-tooltip :options="tooltipOptions">
                  <div class="tooltip-content">
                    <div>{{ item.name }}</div>
                  </div>
                </l-tooltip>
                <!-- 點擊圖標會彈出顯示的文字 -->
                <!-- <l-popup style="text-align: center">
              <div>{{ item.name }}</div>
              <div>目前有效容量比 : {{ text[index].percent }}%</div>
            </l-popup> -->
              </l-marker>
            </l-map>
          </el-col>
          <el-col :span="16">
            <div v-for="item in searchtTableIndex" :key="item.name">
              <div>{{ item.name }}</div>
              <el-table :data="item.data" style="width: 100%">
                <el-table-column prop="ItemName" label="檢測物質" width="180">
                </el-table-column>
                <el-table-column prop="ItemUnit" label="單位" width="180">
                </el-table-column>
                <el-table-column prop="ItemValue" label="數值">
                </el-table-column>
                <el-table-column prop="SampleDate" label="檢測日期">
                </el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";

export default {
  mounted() {
    this.water();
    this.getCTSIdata(
      "https://data.epa.gov.tw/api/v1/stat_p_124?filters=item2,LIKE,澄清湖&limit=1000&api_key=84021832-49ee-4390-9832-1f396d60c47a",
      "澄清湖水庫",
      1
    );
    this.getCTSIdata(
      "https://data.epa.gov.tw/api/v1/stat_p_124?filters=item2,LIKE,鳳&limit=1000&api_key=84021832-49ee-4390-9832-1f396d60c47a",
      "鳳山水庫",
      2
    );
  },
  computed: {
    ...mapState({
      nowChooseIndex: (state) => state.nowChooseIndex,
    }),
  },
  watch: {
    searchData: {
      handler(searchData) {
        this.searchDamYear = [];
        switch (searchData.dam) {
          case "阿公店水庫":
            for (let i = 2007; i <= 2020; i++) {
              this.searchDamYear.push(i);
            }
            break;
          case "澄清湖水庫":
            for (let i = 2005; i <= 2020; i++) {
              this.searchDamYear.push(i);
            }
            break;
          case "鳳山水庫":
            for (let i = 2005; i <= 2020; i++) {
              this.searchDamYear.push(i);
            }
            break;
        }
      },
      deep: true,
    },
  },
  data() {
    return {
      searchtTableTime: [],
      searchtTableIndex: [],
      searchtTableData: {
        ChengChingHu: [],
        AKungTien: [],
        FengShan: [],
      },
      searchData: {
        dam: "",
        year: "",
      },
      searchDamName: ["阿公店水庫", "澄清湖水庫", "鳳山水庫"],
      searchDamYear: [],
      DamMapData: [],
      // 卡爾森指數說明表格
      CTSI_tableData: [
        { CTSI: "CTSI < 40", degree: "貧養狀態" },
        { CTSI: "40 ≦ CTSI ≦ 50", degree: "普養狀態" },
        { CTSI: "CTSI > 50", degree: "優養狀態" },
      ],
      // 卡爾森指數圖表
      ChartOption: {
        title: {
          text: "100-108年高雄水庫卡爾森指數紀錄", //主標題
          textStyle: {
            color: "#006030",
            fontSize: 16,
            align: "center",
          },
          subtext: "*目前政府'無'阿公店水庫卡爾森指數紀錄", //副標題
          subtextStyle: {
            color: "#642100",
            fontSize: 14,
            align: "center",
          },
          itemGap: 7,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
        },
        series: [],
      },
      activeNames: ["1"],
      iconExplain: [
        "目前有效容量比 < 30%",
        "30% < 目前有效容量比 < 80%",
        "目前有效容量比 > 80%",
      ],
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, 10],
      },
      getDataFinished: false,
      // ---------------------水庫資料---------------------
      text: [],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 9,
      //地圖中心
      center: [22.712786, 120.342959],
      // 地標圖示
      iconimg: [
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png",
      ],
      // iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "阿公店水庫",
          local: [22.812786, 120.342959],
        },
        {
          name: "澄清湖水庫",
          local: [22.660768, 120.353766],
        },
        {
          name: "鳳山水庫",
          local: [22.535315, 120.384482],
        },
      ],
    };
  },
  methods: {
    async search() {
      console.log(this.searchData.dam);
      console.log(this.searchData.year);
      switch (this.searchData.dam) {
        case "阿公店水庫":
          this.searchtTableIndex = [
            { name: "阿公店水庫一", data: [] },
            { name: "阿公店水庫二", data: [] },
            { name: "阿公店水庫三", data: [] },
          ];
          break;
        case "澄清湖水庫":
          this.searchtTableIndex = [
            { name: "澄清湖水庫一", data: [] },
            { name: "澄清湖水庫二", data: [] },
            { name: "澄清湖水庫三", data: [] },
            { name: "澄清湖水庫四", data: [] },
          ];
          break;
        case "鳳山水庫":
          this.searchtTableIndex = [
            { name: "鳳山水庫一", data: [] },
            { name: "鳳山水庫二", data: [] },
            { name: "鳳山水庫三", data: [] },
            { name: "鳳山水庫四", data: [] },
          ];
          break;
      }
      const cors = "https://cors-anywhere.herokuapp.com/";
      let headers = {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": true,
      };
      // let chartXdata = this.ChartOption.xAxis.data;
      await axios
        .post(
          `${cors}https://data.epa.gov.tw/api/v1/wqx_p_03?filters=DamName,LIKE,${this.searchData.dam}|SampleDate,LIKE,${this.searchData.year}&sort=SampleDate&limit=1000&api_key=84021832-49ee-4390-9832-1f396d60c47a`,
          headers
        )
        .then((res) => {
          console.log(res.data);
          res.data.records.forEach((item) => {
            if (
              item.ItemName === "透明度" ||
              item.ItemName === "葉綠素a" ||
              item.ItemName === "總磷" ||
              item.ItemName === "卡爾森指數"
            ) {
              if (!this.searchtTableTime.includes(item.SampleDate)) {
                this.searchtTableTime.push(item.SampleDate);
              }
              let detail = {
                ItemName: item.ItemName,
                ItemValue: item.ItemValue,
                ItemUnit: item.ItemUnit,
                SampleDate: item.SampleDate,
              };
              for (let i = 0; i <= this.searchtTableIndex.length - 1; i++) {
                console.log(this.searchtTableIndex[i]);
                console.log(item.SiteName);
                if (item.SiteName === this.searchtTableIndex[i].name) {
                  this.searchtTableIndex[i].data.push(detail);
                }
              }
            }
          });
          console.log(this.searchtTableIndex);
          console.log(this.searchtTableTime);
          //   switch (this.searchData.dam) {
          // case "阿公店水庫":
          //   this.searchtTableIndex = [
          //     { name: "阿公店水庫一", data: [] },
          //     { name: "阿公店水庫二", data: [] },
          //     { name: "阿公店水庫三", data: [] },
          //   ];
          //   break;
          // case "澄清湖水庫":
          //   this.searchtTableIndex = [
          //     { name: "澄清湖水庫一", data: [] },
          //     { name: "澄清湖水庫二", data: [] },
          //     { name: "澄清湖水庫三", data: [] },
          //     { name: "澄清湖水庫四", data: [] },
          //   ];
          //   break;
          // case "鳳山水庫":
          //   this.searchtTableIndex = [
          //     { name: "鳳山水庫一", data: [] },
          //     { name: "鳳山水庫二", data: [] },
          //     { name: "鳳山水庫三", data: [] },
          //     { name: "鳳山水庫四", data: [] },
          //   ];
          //   break;
          // let data1 = {
          //   name: damName,
          //   data: [],
          //   type: "line",
          // };
          // res.data.records.forEach((item) => {
          //   data1.data.push(item.value1);
          //   if (searchIndex === 1) {
          //     chartXdata.push(item.item1);
          //   }
          // });
          // this.ChartOption.series.push(data1);
          // this.ChartOption.title.text = `${chartXdata[0]}-${
          //   chartXdata[data1.data.length - 1]
          // }高雄水庫卡爾森指數紀錄`;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得卡爾森指數資料
    async getCTSIdata(url, damName, searchIndex) {
      const cors = "https://cors-anywhere.herokuapp.com/";
      let headers = {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": true,
      };
      let chartXdata = this.ChartOption.xAxis.data;
      await axios
        .post(`${cors}${url}`, headers)
        .then((res) => {
          console.log(res.data);
          let data1 = {
            name: damName,
            data: [],
            type: "line",
          };
          res.data.records.forEach((item) => {
            data1.data.push(item.value1);
            if (searchIndex === 1) {
              chartXdata.push(item.item1);
            }
          });
          this.ChartOption.series.push(data1);
          this.ChartOption.title.text = `${chartXdata[0]}-${
            chartXdata[data1.data.length - 1]
          }高雄水庫卡爾森指數紀錄`;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 根據水庫的目前有效容量比，更改地圖上icon顏色
    iconColor(index) {
      if (this.text[index].percent < 30) {
        return this.iconimg[0];
      } else if (
        this.text[index].percent > 30 &&
        this.text[index].percent < 80
      ) {
        return this.iconimg[1];
      } else {
        return this.iconimg[2];
      }
    },
    // 各canvas滑鼠移入移出opacity改變效果
    animation(status, element) {
      switch (element) {
        case "sineCanvas":
          var item = document.getElementById("sineCanvas");
          if (status === "in") {
            item.style.opacity = 1;
          } else {
            item.style.opacity = 0.5;
          }
          break;
        case "sineCanvas2":
          var item2 = document.getElementById("sineCanvas2");
          if (status === "in") {
            item2.style.opacity = 1;
          } else {
            item2.style.opacity = 0.5;
          }
          break;
        case "sineCanvas3":
          var item3 = document.getElementById("sineCanvas3");
          if (status === "in") {
            item3.style.opacity = 1;
          } else {
            item3.style.opacity = 0.5;
          }
          break;
        case "originalpic":
          var item4 = document.getElementById("originalpic");
          if (status === "in") {
            item4.style.opacity = 1;
          } else {
            item4.style.opacity = 0.5;
          }
          break;
        case "originalpic2":
          var item5 = document.getElementById("originalpic2");
          if (status === "in") {
            item5.style.opacity = 1;
          } else {
            item5.style.opacity = 0.5;
          }
          break;
        case "originalpic3":
          var item6 = document.getElementById("originalpic3");
          if (status === "in") {
            item6.style.opacity = 1;
          } else {
            item6.style.opacity = 0.5;
          }
          break;
      }
    },

    //     //爬蟲
    //     start() {
    //       var req = new XMLHttpRequest();
    //       req.open("Post", "./test2.py", true);
    //       req.send();
    //     },

    async water() {
      // 取得水庫資料
      await axios
        .get("server/water.json")
        .then((res) => {
          this.text = res.data;
          console.log(this.text);
        })
        .catch((err) => {
          console.log(err);
        });
      this.getDataFinished = true;
      // 改變各水庫有效容量比顏色
      window.setTimeout(() => {
        var percent1 = document.getElementById("percent1");
        var percent2 = document.getElementById("percent2");
        var percent3 = document.getElementById("percent3");
        if (this.text[0].percent < 30) {
          percent1.style.color = "red";
        } else if (this.text[0].percent > 30 && this.text[0].percent < 80) {
          percent1.style.color = "#DAA520";
        } else {
          percent1.style.color = "green";
        }
        if (this.text[1].percent < 30) {
          percent2.style.color = "red";
        } else if (this.text[1].percent > 30 && this.text[1].percent < 80) {
          percent2.style.color = "#DAA520";
        } else {
          percent2.style.color = "green";
        }
        if (this.text[2].percent < 30) {
          percent3.style.color = "red";
        } else if (this.text[2].percent > 30 && this.text[2].percent < 80) {
          percent3.style.color = "#DAA520";
        } else {
          percent3.style.color = "green";
        }

        this.drawWater("1", this.text[0].percent);
        this.drawWater("2", this.text[1].percent);
        this.drawWater("3", this.text[2].percent);
      }, 0);
    },
    //水填滿canvas
    drawWater(name, percent) {
      var unit = 100,
        canvas,
        context,
        height,
        width,
        xAxis,
        yAxis;
      var percentMax = percent / 100; //液體占容器的百分比
      console.log("percentMax=" + percentMax);
      var percentMin = 0;
      var maxHeight = 1;
      if (percentMax >= 1) {
        maxHeight = 0.95;
      }
      var wavePower = 3;

      //初始化
      function init() {
        window.requestAnimFrame = (function () {
          return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            function (callback) {
              window.setTimeout(callback, 1000 / 60);
            }
          );
        })();

        if (name == "1") {
          canvas = document.getElementById("sineCanvas");
        } else if (name == "2") {
          canvas = document.getElementById("sineCanvas2");
        } else {
          canvas = document.getElementById("sineCanvas3");
        }

        canvas.width = 1000;
        canvas.height = 1000;

        context = canvas.getContext("2d");

        height = canvas.height;
        width = canvas.width;
        yAxis = 0;

        draw();
      }
      function draw() {
        // 清理canvas畫板
        context.clearRect(0, 0, width, height);

        //描繪波浪
        if (percent > 80) {
          drawWave(["#77DDFF", "#0044BB"], 1, 1.5);
        } else if (percent > 30 && percent < 80) {
          drawWave(["#F0E68C", "#B8860B"], 1, 1.5);
        } else {
          drawWave(["#FFA07A", "#FF4500"], 1, 1.5);
        }

        if (percentMin <= percentMax) {
          percentMin = percentMin + 0.002;
          xAxis = Math.floor(height * (maxHeight - percentMin));
        }
        if (percentMin > 0.8) {
          if (wavePower <= 36 * percentMax) {
            wavePower = wavePower + 0.05;
          }
        }

        // 更新時間然後重新繪制
        draw.seconds = draw.seconds + 0.009;
        draw.t = draw.seconds * Math.PI;
        window.requestAnimFrame(draw);
      }
      draw.seconds = 0;
      draw.t = 0;

      /**
       * 描繪波浪
       * drawWave([漸變左側颜色，右側颜色], 不透明度, 波浪的幅度)
       */
      function drawWave(color, alpha, zoom) {
        context.globalAlpha = alpha;
        var linearGrad = context.createLinearGradient(0, 0, width, 0);
        linearGrad.addColorStop(0.0, color[0]);
        linearGrad.addColorStop(1.0, color[1]);
        context.fillStyle = linearGrad;

        context.beginPath(); //開始路徑
        drawSine(draw.t / 0.5, zoom);
        context.lineTo(width + 10, height); //路徑朝canvas右下
        context.lineTo(0, height); //路徑朝canvas左下
        context.closePath(); //封閉路徑
        context.fill(); //塗色
      }

      /**
       * 畫正弦
       * drawSine(時間, 波浪的浮動)
       */
      function drawSine(t, zoom) {
        var x = t; //將時間轉換為x軸
        var y = Math.sin(x) / zoom;
        context.moveTo(yAxis, unit * y + xAxis); //在開始位置放置路径

        // 繪制波浪、横向幅度
        for (var i = yAxis; i <= width + 10; i += 10) {
          x = t + (-yAxis + i) / unit / zoom;
          y = Math.sin(x - 0) / wavePower;
          context.lineTo(i, unit * y + xAxis);
        }
      }
      init();
    },
  },
};
</script>
